import React from 'react';
import { Row, Col, Carousel, MediaBox } from 'react-materialize';
import M from 'materialize-css';
import Axios from 'axios';

import './Section.css';
import './Howami.css';
import './Services.css';
/* Improviso do componente card */
import './Card.css';
import './Certificate.css';



class Section extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.id,
            class: "leyd-section " + props.className,
            style: {
                backgroundColor: props.background,
                display: 'block'
            }
        }
    }

    render() {
        return (
            <section id={this.state.id} style={this.state.style} className={this.state.class}>
                <Row className={this.props.classNameRow}>
                    {this.props.children}
                </Row>
            </section>
        );
    }
}

