export class Services extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            listServices: []
        }
    }
    componentWillMount() {
        Axios.get('/assets/data/services.json')
            .then((res) => {
                this.setState({ listServices: res.data });
            })
            .catch((err) => {
                console.log(err);
            });
    }

    mapList(list, col = false) {
        let jsx;
        if (!col) {
            jsx = list.map((srv) =>
                (
                    <div key={srv.legen} style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <div style={{ display: 'block' }}>
                            <figure className="leyd-figure-service">
                                <img src={srv.imgem} alt="" className="leyd-img-figure-service" />
                                <figcaption className="black-text">{srv.legen}</figcaption>
                            </figure>
                            <p className="center black-text">
                                {srv.desc}
                            </p>
                        </div>
                    </div>
                )
            );
        } else {
            jsx = list.map((srv) =>
                (
                    <Col m={6} l={6} xl={3} key={srv.legen} className="hide-on-small-only">
                        <figure className="leyd-figure-service">
                            <img src={srv.imgem} alt="" className="leyd-img-figure-service" />
                            <figcaption>{srv.legen}</figcaption>
                        </figure>
                        <p className="center">
                            {srv.desc}
                        </p>
                    </Col>
                )
            );
        }
        return jsx;
    }

    render() {
        const list = this.state.listServices;
        if (list.length) {
            return (
                <Section id="services" classNameRow="center" background="#F7F7F7">
                    <Col s={12} m={12} l={12} xl={12}>
                        <h2 className="center-align blue-text">Services</h2>
                    </Col>
                    {this.mapList(list, true)}
                    <Col s={12} m={12} className="hide-on-med-and-up">
                        <Carousel options={{ fullWidth: false, dist: 0, shift: 50 }} className=" white-text center">
                            {this.mapList(list, false)}
                        </Carousel>
                    </Col>
                </Section>
            );
        } else {
            return null;
        }
    }
}

/** Improviso do componente card
  * Detalhes em: https://github.com/react-materialize/react-materialize/issues/888
  */
class Card extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            img: props.img,
            title: props.title,
            description: props.description,
            link: props.link
        }
    }
    render() {
        return (
            <div className="card hoverable" style={{ display: 'inline-block' }}>
                <div className="card-image" >
                    <img src={this.state.img} alt="" className="leyd-img-card" />
                    <a className="btn-floating halfway-fab waves-effect waves-light cyan pulse" href={this.state.link} rel="noopener noreferrer" target="_blank"><i className="material-icons">launch</i></a>
                </div>
                <div className="card-content" >
                    <span className="card-title black-text">{this.state.title}</span>
                    <p>{this.props.children}</p>
                </div>
            </div>
        );
    }
}

export class Portfolio extends React.Component {
    constructor(props) {
        super(props);
        this.WrapCarousel = function (props) {
            return (<div>{props.childrens}</div>)
        }
        this.state = {
            listPortf: []
        }
    }
    componentWillMount() {
        Axios.get('/assets/data/portfolio.json')
            .then((res) => {
                this.setState({ listPortf: res.data })
            })
            .catch((err) => {
                console.log(err);
            })
    }
    mapList(list, col = false) {
        let jsx;
        if (!col) {
            jsx = list.map((portf) =>
                <div key={portf.title} style={{ display: 'flex', alignItems: 'center' }}>
                    <Card
                        link={portf.link}
                        img={portf.urlImg}
                        title={portf.title}>
                    </Card>
                </div>
            );
        } else {
            jsx = list.map((portf) =>
                <Col l={3} xl={3} key={portf.title} className="hide-on-med-and-down">
                    <Card
                        link={portf.link}
                        img={portf.urlImg}
                        title={portf.title}>
                        {portf.desc}
                    </Card>
                </Col>
            );
        }
        return jsx;
    }
    render() {
        const list = this.state.listPortf;
        if (list.length) {
            return (
                <Section id="portfolio">
                    <Col s={12} m={12} l={12} xl={12}>
                        <h2 className="blue-text">Portfólio</h2>
                    </Col>
                    {this.mapList(list, true)}
                    <Col s={12} m={12} className="hide-on-large-only">
                        <Carousel options={{ fullWidth: false, dist: 0, shift: 30 }} className=" white-text center">
                            {this.mapList(list, false)}
                        </Carousel>
                    </Col>
                </Section>
            );
        } else {
            return null
        }
    }
}

export class Certificate extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            ref: React.createRef(),
            prepared: false,
            listCert: []
        }
    }
    componentWillMount() {
        Axios.get('/assets/data/certificates.json')
            .then((res) => {
                this.setState({
                    listCert: res.data,
                    prepared: true
                })
            })
            .catch((err) => {
                console.log(err);
            })
    }

    mapList(list) {

        const jsx = list.map((cert) => (
            <div key={cert.urlImg} className="leyd-container-carousel-certificate">
                <a href={cert.urlImg} target="_blank">
                    <img src={cert.urlImg} className="leyd-slide-carousel-certificate" alt="" />
                </a>
            </div>
        ));
        return jsx;
    }
    componentDidUpdate() {
        if (this.state.prepared) {
            var elem = this.refs['carousel1'].instance.el;
            var instance = M.Carousel.getInstance(elem);

            let timerID
            function startTimer() {
                timerID = setInterval(() => {
                    instance.next(1)
                }, 3000);
            };

            elem.addEventListener('mouseover', () => {
                clearInterval(timerID);
            })
            elem.addEventListener('mouseout', () => {
                startTimer();
            })
            startTimer();

        }
    }

    render() {

        const list = this.state.listCert;
        if (list.length) {
            return (
                <Section id="certificates" background="#F7F7F7">
                    <Col s={12} m={12} l={12} xl={12} >
                        <h2 className="center blue-text">Certificados</h2>
                    </Col>
                    <Col s={12} m={12} l={12} xl={12} className="">

                        <Carousel carouselId="chatoPraCaralho" ref="carousel1" options={{ fullWidth: false }} className="leyd-carousel-certificate white-text center">
                            {this.mapList(list)}
                        </Carousel>
                    </Col>
                </Section>
            );
        } else {
            return null;
        }
    }
}