import React from 'react';

export class Navbar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fixed: props.fixed ? true : false
        }
    }

    render() {
    
        const nav = (
            <nav className={this.props.className}>
                <div className="nav-wrapper">
                    <a href="#!" className="brand-logo">Leydev</a>
                    <a href="#!" data-target="mobile-demo" className="sidenav-trigger"><i className="material-icons">menu</i></a>
                    <ul className="right hide-on-med-and-down">
                        <li><a href="sass.html">How Am I</a></li>
                        <li><a href="badges.html">Serviços</a></li>
                        <li><a href="badges.html">Portfólio</a></li>
                        <li><a href="badges.html">Certificados</a></li>
                        <li><a href="badges.html"><i className="fab fa-linkedin-in"></i></a></li>
                        <li><a href="badges.html"><i className="fab fa-github"></i></a></li>
                    </ul>
                </div>
            </nav>
        )
        if (this.state.fixed) {
            return (
                <div className="navbar-fixed">
                    {nav}
                </div>);
        } else {
            return nav;
        }
    }
}